<!-- 人口流动弦图 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>人口流动弦图</h3>
      <div id="container"></div>
    </div>
  </div>
</template>

<script>
import { Chord } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const DATA = [
      { source: '北京', target: '天津', value: 30 },
      { source: '北京', target: '上海', value: 80 },
      { source: '北京', target: '河北', value: 46 },
      { source: '北京', target: '辽宁', value: 49 },
      { source: '北京', target: '黑龙江', value: 69 },
      { source: '北京', target: '吉林', value: 19 },
      { source: '天津', target: '河北', value: 62 },
      { source: '天津', target: '辽宁', value: 82 },
      { source: '天津', target: '上海', value: 16 },
      { source: '上海', target: '黑龙江', value: 16 },
      { source: '河北', target: '黑龙江', value: 76 },
      { source: '河北', target: '内蒙古', value: 24 },
      { source: '内蒙古', target: '北京', value: 32 }
    ]

    const chord = new Chord('container', {
      data: DATA,
      sourceField: 'source',
      targetField: 'target',
      weightField: 'value'
    })

    chord.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGs {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 800px;
    height: 400px;
  }

  #containerpt {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGet {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerTexture {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



